  uploadAndProgress(files: File[]){
    console.log(files)
    var formData = new FormData();
    Array.from(files).forEach(f => formData.append('file',f))
    var dest_file = new Date().getTime() + ".csv";
    this.http.post('https://cockburncrc.com.au/members/usermanagement/uploadCsv.php?fld=csv&file=' + dest_file, formData, {reportProgress: true, observe: 'events'})
      .subscribe(event => {
        if (event.type === HttpEventType.UploadProgress) {
          //this.percentDone = Math.round(100 * event.loaded / event.total);
        } else if (event instanceof HttpResponse) {
          //this.uploadSuccess = true;
        }
        if(event.type == 3)
        {
          var path = "uploads/csv/" + dest_file;
          //alert(path);
          this.transactionsservice.getByCsv(path,'local').then(data => {
              this.json = data["data"];
              this.CreateTableFromJSON(data["data"]);
          });
        }
    });
    // this.fileName = file;
    // alert(this.fileName);
  }

